const main = document.querySelector('main');

const createUl = document.createElement('ul');
createUl.classList.add('gallery');

const elements = images.map(image => {
  const createLi = document.createElement('li');
  createLi.classList.add('gallery-item');

  const createLink = document.createElement('a');
  createLink.classList.add('gallery-link');
  createLink.href = image.original;
  createLink.addEventListener('click', event => event.preventDefault());

  const createImg = document.createElement('img');
  createImg.classList.add('gallery-image');
  createImg.src = image.preview;
  createImg.alt = image.description;
  createImg.setAttribute('data-source', image.original);

  createLink.append(createImg);
  createLi.append(createLink);

  return createLi;
});

createUl.append(...elements);
main.append(createUl);

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt', // Підпис бере з атрибута alt
  captionDelay: 250, // Затримка перед показом підпису (мс)
});
